import { newE2EPage } from '@stencil/core/testing';

describe('nylas-threads-refresh', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<nylas-threads-refresh></nylas-threads-refresh>');

    const element = await page.find('nylas-threads-refresh');
    expect(element).toHaveClass('hydrated');
  });
});
